import "font-awesome/css/font-awesome.css";
import "./printer.css";
import * as Modal from "../console/modal";

window.Modal = Modal;

let printer = { 
    socket: null,
    myTextDecoder: null,
    elmnt: null,
    line: '',
    lineNum: 0,
    formLength: 66,
    paper: 0,
    stock: [ 'blue', 'green', 'white' ],

    paperChange: function () {
        let elmt = document.querySelectorAll("body");
        this.paper = (this.paper + 1) % this.stock.length;
        elmt[0].removeAttribute("class");
        elmt[0].setAttribute("class", this.stock[this.paper]);
    },

    attach: function (sock) {
        this.socket = sock;
        this.line = '';
        this.lineNum = 0;
    },
    detach: function (sock) {
        this.socket = null;
        printer.flush();
    },
    write: function (text) {
        // console.log('WRITE');
        if(!this.elmnt) {
            this.elmnt = document.getElementById("printer");
            this.elmnt.style.setProperty('--form-length', this.formLength);
        }
        this.line += text;
        if(text == '\n' || text.length > 1) {
            this.lineNum++;
            printer.flush();
        } else if (text == String.fromCharCode(12)) printer.formFeed();
    },
    flush: function () {
        let node = document.createTextNode(this.line);
        this.elmnt.appendChild(node);
        this.line = '';
        this.elmnt.scrollIntoView(false);
    },
    formFeed: function () {
        printer.flush();
        this.line = "\r\n".repeat(this.formLength-(this.lineNum % this.formLength));
        printer.flush();
        this.lineNum = 0;               
    },
    lineFeed: function () {
        printer.flush();
        this.line = "\r\n";
        printer.flush();
        // this.lineNum = 0;               
    },
    reset: function () {
        if(!this.elmnt) {
            this.elmnt = document.getElementById("printer");
            this.elmnt.style.setProperty('--form-length', this.formLength);
        }
        printer.flush();
        this.lineNum = 0;
        this.elmnt.innerHTML = '';
        this.elmnt.scrollIntoView(false);
    },
    getMessage: function (ev) {
        // var str;
        // console.log(ev);
        // if (typeof ev.data === "object") {
        //     if (ev.data instanceof ArrayBuffer) {
        //         if (!this.myTextDecoder) {
        //             this.myTextDecoder = new TextDecoder();
        //         }

        //         str = this.myTextDecoder.decode( ev.data );
        //     } else {
        //         throw "TODO: handle Blob?";
        //     }
        // }
        // printer.write(str || ev.data);
        printer.write(ev.data);
    }
};

let connectLPT = function() {

    if(printer.socket) {
        Modal.confirm("Disconnect from Printer? [LPT:]", () => {
            let sock = printer.socket;
            sock.close();
            printer.detach(sock);
        });    
    } else {
        var url = (
            "ws://"+location.hostname+(location.port ? ':'+location.port : '')+"/lpt"),
            sock = new WebSocket(url),
            connect = document.getElementById('online');

        sock.addEventListener('open', function () {
            printer.attach(sock);
            connect.setAttribute('style', 'color: rgb(0, 255, 0);');
        });
        sock.addEventListener('close', function () {
            printer.detach(sock);
            connect.setAttribute('style', 'color: rgb(255, 0, 0);');
        });
        sock.addEventListener('message', printer.getMessage);
    }
}

let includeFile = function() {
    let list, len, i, elmnt, file, xhttp;
    list = document.querySelectorAll("div[data-include-file]");
    len = list.length
    for (i = 0; i < len ; i++) {
        elmnt = list[i];
        file = elmnt.getAttribute("data-include-file");
        if (file) {
        xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function() {
            if (this.readyState == XMLHttpRequest.DONE && this.status == 200) {
                for(let p=0; p<this.responseText.length; p++) {
                    printer.write(this.responseText.charAt(p));
                }
            }
        }      
        xhttp.open("GET", file + "?" + (new Date()).getTime(), true);
        xhttp.send();
        return;
        }
    }
};


includeFile();
connectLPT();

window.lpt = printer;
window.lpt.connect = connectLPT;
